.comments {
    max-width: 562px;
    width: 100%;
    padding: 32px 0 64px;
    margin: auto;
}

.stat {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    padding: 20px 0 10px;
    margin-bottom: 32px;
    border-bottom: 1px solid #767676;
    color: #fff;
    backdrop-filter: blur(3px);
    font-weight: 700;
}

.allLikes {
    display: flex;
    align-items: center;
    gap: 8px;
}

.items {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 32px;
    align-items: start;
    margin-bottom: 60px;
}

.bottom {
    display: flex;
    justify-content: center;
}
